html, body, .App, #root {
    height: 100%;
    max-height: auto;

}
html {
    box-sizing: border-box;
  }
  *, *:before, *:after {
    box-sizing: inherit;
  }

:root {
    --AzulPrincipal: #00264C;
    --AzulSecundario: #26609b;
    --BlancoPrincipal: #C9BBCA;
    --BlancoSecundario: #cecece;
}
